#about {
  background: #c0392b;
  margin-bottom: 50px;

  .main-about {
    text-transform: uppercase;
    color: #fff;
    font-family: $font-stack-heading;
    font-weight: 600;
    font-size: 2.5rem;
    text-align: center;
    padding-top: 80px;

    hr {
      max-width: 50px; } }

  .about-content {

    box-sizing: border-box;
    display: flex;
    justify-content: space-around;
    margin: 20px auto;
    max-width: 600px;
    padding-bottom: 80px;

    .content {
      padding: 20px;
      width: 100%;
      color: #fff;
      font-family: $font-stack-heading;
      font-weight: 300;
      letter-spacing: 0.5px;
      font-size: .8rem;
      line-height: 1.5rem; } } }

@media (max-width: 600px) {
    .about-content {
      flex-wrap: wrap; } }
