
/* PORTFOLIO */
#portfolio {

  margin-bottom: 50px;

  .main-head {
    text-transform: uppercase;
    color: #333;
    font-family: $font-stack-heading;
    font-weight: 600;
    font-size: 2.5rem;
    text-align: center;
    padding-top: 50px;

    hr {
      max-width: 50px; } }
  .image-container {
    margin: 20px auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;

    .item {
      max-width: 360px;
      height: 260px;
      display: flex;
      margin-bottom: 20px;

      img {
        width: 100%; } } } }
