/* Portfolio */

#header {
  height: 100vh;
  background: #c0392b;
  position: relative;
  .header-section {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    .profile {
      text-align: center;
      img {
        border-radius: 50%;
        box-shadow: inset 0 1px 5px rgba(0,0,0,.55); } }


    .heading-1 {
      color: white;
      font-family: $font-stack-heading;
      text-align: center;
      text-transform: uppercase;
      margin-top: 20px;
      // padding: 5px
      font-weight: bold;
      letter-spacing: 2px;
      font-size: 4rem; }

    .heading-2 {
      color: white;
      text-transform: uppercase;
      letter-spacing: 2px;
      font-family: $font-stack-heading;
      font-weight: 300;
      font-size: 1rem;
      margin-top: 10px;
      padding: 5px;
      text-align: center;
      line-height: 1.5rem; }

    .star-line {
      color: white;
      max-width: 20rem; } } }

