@media (max-width: 600px) {
  #header {
    .header-section {
      .heading-2 {
        text-align: center;
        line-height: 1.2rem;
        font-size: 2.7vw; } } } }

@media (max-width: 400px) {
    #header {
      .header-section {
        .heading-1 {
          font-size: 2.2rem; } } } }
